@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);

body{
  padding: 100px;
  font-family: 'Roboto',sans-serif;
  font-weight: 300;
  height:100vh;


  background:
  	repeating-radial-gradient(
  		circle,
  		transparent,
  		transparent 3.5em,
  		rgba(255, 99, 71, 0.1) 3.5em,
  		rgba(255, 99, 71, 0.1) 4.5em
  	),
  	repeating-radial-gradient(
  		circle,
  		transparent,
  		transparent 3.5em,
  		rgba(141, 199, 255, 0.1) 3.5em,
  		rgba(141, 199, 255, 0.1) 4.5em
  	),
  	repeating-radial-gradient(
  		circle,
  		transparent,
  		transparent 2.5em,
  		rgba(240, 224, 137, 0.1) 2.5em,
  		rgba(240, 224, 137, 0.1) 2.75em
  	), floralwhite;

  background-blend-mode: multiply;
  background-size: 10em 10em;
  background-position:
  	0 0,
  	5em 5em,
  	10em 5em;
}
h1.greet-message{
  text-align: center;
  font-weight: 300;
}
ul.list-group{
  list-style: none;
  margin:20px auto;
  padding:0;
  text-align: center;
  li{
    display: inline-block;
    padding: 10px;
    background: #FFEB3B;
    margin: 0 5px;
    font-size:30px;
    /*transform: skewX(7deg) skewY(2deg);*/
  }
}
.fork-me{
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  max-width: 50%;
  cursor:pointer;
  img{
    max-width: 100%;
    cursor:pointer;
  }

}
.playground{
  width: 90%;
  background: white;
  height: 90%;
  max-width: 1100px;
  max-height: 500px;
  margin: auto;
  box-shadow: 0px 0px 43px -17px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.playground div.col-md-6:first-child{
  background: #f0f0f0;
}
.playground div.col-md-6{
  padding:50px;height: 100%;
}
ul.list-group.active_vectors_bar{
  position: fixed;
  bottom: 0;
  width: 100%;
  transform: translateX(-50%);
  left: 50%;
  margin: 0;
  background: #1e8df0;
  padding: 10px 10px 0;
  border-radius: 10px 10px 0 0;
}
.card_wrap{
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.card_border{padding:30px;}
.card_border{
  padding: 20px;
  border: 10px solid #ffcc00;
  background: #ffffff;
  border-radius: 50%;
  position: absolute;
  transform: translate(-50%,-50%);
  left: 50%;
  top: 50%;
  width: 300px;
  height: 300px;
}
.card_border>div{
  position: relative;
  display:block;
  width: 100%;
  height: 100%;
}
.card_border>div{    padding: 20px;
    box-sizing: border-box;
    border-radius: 50%;}
.card_border,.card_border a{transform: translate(-50%,-50%);}
.card_border a {
    position: absolute;
    font-size: 40px;
}
.card_border a:hover{
    text-decoration: none;}

.game_header{
  position: absolute;
  top: 0;
  left: 0;
  max-width: 90%;
  width: 1000px;
  padding: 20px;
  box-sizing: border-box;
  z-index: 1;
  background: white;
  border-radius: 0 0 30px 0;
  box-shadow: 0px 0px 63px -15px;
  line-height:60px;
  font-weight: 100;
}
.game_header h2{margin:0;font-weight: 100;line-height:60px;font-size:60px;}
